import { createContext, useState, useEffect } from "react";
import {keychain, isKeychainInstalled, hasKeychainBeenUsed} from '@hiveio/keychain'
const AuthContext = createContext();
const initialAuth = null;
const AuthProvider = ({ children }) => {
const [auth, setAuth] = useState(initialAuth);
const [user, setUser] = useState(initialAuth);


   

  const handleAuth = async (e) => {
    const {auth ,name, value } = e.target;
 if (auth) {
      
        console.log(e.target)
      setAuth(null);
      setUser("");
    } else {

    if (!Object.prototype.hasOwnProperty.call(window, "hive_keychain")) {
      alert("please install hive keychain");

      return;
    }

    if (auth) {
      alert("please write a username");
      return;
      console.log(e.target)
    }
    let userr="alvarogo";
    console.log(e)
    window.hive_keychain.requestSignBuffer(
      userr,
      "Login to hashkings",
      "Posting",
      async (response) => {
        if (response.success === true) {
          //


 



          setUser(userr);
          console("Este es el resultado del Success por entrar a mi pagina")
        } else {
          // window.location.reload();
        }
      }
    );
      setAuth(true);
    }
  };

  const data = { auth,user, handleAuth };

  return <AuthContext.Provider value={data}>{children}</AuthContext.Provider>;
};

export { AuthProvider };
export default AuthContext;
