import { useContext } from "react";

import LanguageContext from "../context/LanguageContext";
import ThemeContext from "../context/ThemeContext";
import './main.css'
import Home from "./componets/home/Home.jsx"
import Presales from"./componets/presales/Presales.jsx"

let maincss="maquillaje"

const MainContext = () => {

const { theme } = useContext(ThemeContext);

const { texts } = useContext(LanguageContext);

const auth=localStorage.getItem('auth')

const user=localStorage.getItem("user")

theme==="dark" ?  maincss="maquillaje-dark":   maincss="maquillaje"


  return (
    <main className={maincss}>

       {auth ? <p>{texts.mainHello + user} </p> : <p className={theme}>{texts.mainWelcome}</p>}
      
     { auth ? <Presales/> :  <Home/>   }

     { <p>{texts.mainContext}</p>}
    
   </main>

  );
};

export default MainContext;
