import React from 'react'
import{NavLink} from'react-router-dom'
import { useState } from "react"
import "./navbar.css"

//import Login from './components/Login.jsx'

//import AuthenticationView from './components/Logout.jsx'








  let activeStyle = {
    textDecoration: "underline",
  };

  let activeClassName = "underline";






export default function Navbar(props) {
  const [isNavExpanded, setIsNavExpanded] = useState(false)
   //console.log(isNavExpanded)
 
  return (         
   
    <nav className={[props.theme, "navigation"].join(' ')}>
<NavLink to="/"className="brand-name">
        Horses
      </NavLink>
      <button
        className="hamburger"
        onClick={() => {
          setIsNavExpanded(!isNavExpanded)
        }}
      >
            <svg
          xmlns="http://www.w3.org/2000/svg"
          className="h-5 w-5"
          viewBox="0 0 20 20"
          fill="white"
        ><path
            fillRule="evenodd"
            d="M3 5a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM3 10a1 1 0 011-1h12a1 1 0 110 2H4a1 1 0 01-1-1zM9 15a1 1 0 011-1h6a1 1 0 110 2h-6a1 1 0 01-1-1z"
            clipRule="evenodd"
          />
        </svg>
      </button>
      <div
        className={
          isNavExpanded ? "navigation-menu expanded" : "navigation-menu"
        }
      >
        <ul className={props.theme}>
          <li>
            <NavLink  end to="/"   style={({ isActive }) =>
              isActive ? activeStyle : undefined
            }>Presale</NavLink>
          </li>
          <li>
            <NavLink end  to="/dashboard"  style={({ isActive }) =>
              isActive ? activeStyle : undefined
            }>Dashboard</NavLink>
          </li>
          <li>
            <NavLink end  to="/discord"  style={({ isActive }) =>
              isActive ? activeStyle : undefined
            }>Discord </NavLink>
          </li>
         
            
        </ul>
      </div>
    </nav>
     
  );
}