
import { useFormLogin } from "../../hooks/useFormLogin.jsx";


 

const initialForm = {
  name: "",
 };

const validationsForm = (form) => {
  let errors = {};
  let regexName = /^[A-Za-z\s]+$/;
 if (!form.name.trim()) {
    errors.name = "El campo 'Nombre' es requerido";
  } else if (!regexName.test(form.name.trim())) {
    errors.name = "El campo 'Nombre' slo acepta letras y espacios en blanco";
  }
 return errors;
};

let styles = {
  fontWeight: "bold",
  color: "#dc3545",
};

const LoginForm = () => {

 const {
    form,
    errors,

    handleChange,
    handleBlur,
    handleSubmit,
  } = useFormLogin(initialForm, validationsForm);

  return (
    <div>
      <h2>Ingresa tu usuario Hive</h2>
      <form onSubmit={handleSubmit}>
        <input
          type="text"
          name="name"
          placeholder="Escribe tu nombre"
          onBlur={handleBlur}
          onChange={handleChange}
          value={form.name}
          required
        />
       
        {errors.name && <p style={styles}>{errors.name}</p>}
        <input type="submit"  value="Go" />
       
      </form>
   {/*   {loading && <Loader />}
      {response && (
        <Message msg="Los datos han sido enviados" bgColor="#198754" />
      )}*/}
    </div>
  );
};

export default LoginForm;

